import React, { useRef } from "react";
import { Link } from "gatsby";
import styled from "styled-components";
import kebabCase from "lodash/kebabCase";
import useScrollCenter from "hooks/useScrollCenter";
import { ACTIVE } from "constants/constants";

const CategoryFilter = ({ categoryList }) => {
  const categoryRef = useRef(null);
  const ALL_CATEGORY_NAME = "All";
  const isActive = ({ isCurrent }) =>
    isCurrent ? { id: ACTIVE, tabIndex: -1 } : {};

  useScrollCenter({ ref: categoryRef, targetId: ACTIVE });

  return (
    <Nav aria-label="Category Filter">
      <CategoryButton getProps={isActive} to="/">
        {ALL_CATEGORY_NAME}
      </CategoryButton>
      <CategoryUl ref={categoryRef} className="invisible-scrollbar">
        {categoryList
          .sort((a, b) => b.totalCount - a.totalCount)
          .map((category) => {
            const { fieldValue } = category;
            return (
              <li key={fieldValue}>
                <CategoryButton
                  getProps={isActive}
                  to={`/category/${kebabCase(fieldValue)}/`}
                >
                  {fieldValue}
                </CategoryButton>
              </li>
            );
          })}
      </CategoryUl>
    </Nav>
  );
};

const Nav = styled.nav`
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  border-radius: var(--border-radius-base);

  a#active {
    color: var(--color-black);
  }
`;

const CategoryButton = styled(Link)`
  cursor: pointer;
  display: block;
  /* background-color: var(--color-category-button); */
  margin-right: 1.625rem;
  border-radius: var(--border-radius-base);
  font-size: 1.625rem;
  font-weight: var(--font-weight-semi-bold);
  color: #808080;

  :focus {
    outline: none;
  }

  &:hover {
    color: var(--color-black);
  }

  &:focus-visible {
    color: var(--color-white);
    background-color: var(--color-blue);
  }
`;

const CategoryUl = styled.ul`
  display: flex;
  list-style: none;
  overflow-x: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;

  li + li {
    margin-left: 6px;
  }

  &::-webkit-scrollbar {
    display: none;
  }
`;

export default CategoryFilter;
